@use '~normalize.css/normalize.css'
@use '~modern-css-reset/dist/reset.min.css'
@use 'fonts'
@use 'container'

@use "../utils/_mixins/vars" as *
@use "../utils/_mixins/media" as *
@use "../utils/theme" as *

ul
  margin: 0
  padding: 0
ul li
  list-style-type: none
a, a:hover, a:focus, a:active
  text-decoration: none
  color: inherit

button
  background: transparent
  margin: 0
  padding: 0
  outline: none
  border: none
  overflow: visible
  color: inherit
  font: inherit
  line-height: normal
  text-align: inherit
  &:focus:not(:focus-visible)
    outline: none
  &:focus:not(:-moz-focusring)
      outline: none

.vvi
  +themed()
    background-color: t(bg)
    color: t(text)
  .vvi-wrap
    display: flex
    padding: 2rem 0
    .vvi-action
      margin-right: 10rem
      &:last-child
        margin-right: 0
      span
        display: block
        font-size: px2em(18)
        margin-bottom: 1rem
    .vvi-btn-group
    .vvi__btn
      cursor: pointer
      background-color: #2196F3
      font-size: px2em(30)
      padding: 10px
      color: white
      &:hover
        font-size: px2em(30)
      &.dark
        background-color: black
        color: white
      &.yellowed
        background-color: #f7f3d6 !important
        color: black
      &.light
        background-color: #9dd1ff !important
        color: black
    +phone()
      flex-direction: column
      .vvi-action
        margin-right: 0
        margin-bottom: 2rem
        span
          font-size: 20px
  &_visible
    display: block!important
