@use '../utils/_mixins/media' as *
@use '../utils/_mixins/vars' as *
@use '../utils/theme' as *

.footer
  +themed()
    background-color: t(footer-bg)
  //background-color: #fbdaa3
  padding-top: 10%
  &-nav
    padding-bottom: 5%
  &-nav-item
    padding-right: 2rem
    +tablet()
      padding-bottom: 2rem
    +phone()
      padding-bottom: 1rem
    h3,h2
      padding-bottom: 2rem
      +themed()
        color: t(footer_h_color)
      +phone()
        padding-bottom: 1.5rem
    ul
      li
        font-size: px2em(18)
        padding-bottom: 1em
        font-family: regular, sans-serif
        +desktop()
          font-size: px2em(16)
        +themed()
          color: t(text-grey2)

        &:hover
          +themed()
            color: t(hover-primary)
        a
          display: flex
          align-items: center
          .icon
            +themed()
              fill: t(hover-primary)
            width: px2em(30)
            height: px2em(30)
            margin-right: 1rem
.footer-about
  width: 100%
  padding: 2% 0
  +themed()
    background-color: t(footer-about)
  color: white
  +tablet()
    padding: 5% 0
  &-content
    display: flex
    align-items: center
    justify-content: space-between
    +phone()
      justify-content: center
  &__title
    display: block
    font-size: px2em(18)
    +phone()
      text-align: center
      font-size: 14px
  &__confines
    display: block
    border-radius: 50%
    padding: px2em(10)
    border: 1px solid white
    +phone()
      display: none


