@use 'header'
@use 'news-module'
@use 'footer'

@use '../utils/_mixins/media' as *
@use '../utils/_mixins/vars' as *
@use '../utils/theme' as *

.main
  .often-viewed
    &__item
      display: flex
      width: calc((100% / 3) - (4rem / 3))
      align-items: center
      padding: px2em(45) px2em(50)
      font-size: px2em(25)
      font-family: medium, sans-serif
      margin-right: 2rem
      transition: background-color .3s
      &:last-child
        margin-right: 0
      .icon
        width: px2em(60px)
        height: px2em(60px)
        +themed()
          fill: t(text-primary)
        margin-right: 1.8rem
      &:hover
        +themed()
          background-color: t(hover-primary)
        color: white
        .icon
          fill: white
      +extra()
        padding: px2em(25) px2em(15)
      +desktop()
        font-size: 20px
      +tablet()
        &:nth-child(2)
          margin-right: 0
        width: calc((100% / 2) - (2rem / 2))
        margin-bottom: 2rem
        &:last-child
          margin-bottom: 0
      +phone()
        padding: px2em(10) px2em(7)
        width: 100%
        margin-right: 0
        .icon
          width: 2.75em
          height: 2.75em


section.index--last-docs
  .main--document-list
    flex-direction: row
    flex-wrap: wrap
  .main--document
    width: calc((100% / 2) - (2rem / 2))
    &:first-child
      margin-right: 2rem
    +extra()
      width: 100%
      &:first-child
        margin-right: 0
  .main--document-info
    justify-content: space-between
    +wide()
      justify-content: flex-start
.main
 .useful-links
  &-wrap
    display: flex
    flex-wrap: wrap
  &__item
    width: calc((100% / 4) - (3rem / 4))
    display: flex
    flex-direction: column
    padding: px2em(40) 0 px2em(40) px2em(25)
    margin-right: 1rem
    margin-bottom: 1rem
    border: 1px solid $border-light
    transition: background-color .3s
    &:nth-child(4n)
      margin-right: 0
    +wide()
      width: calc((100% / 3) - (2rem / 3))
      &:nth-child(4n)
        margin-right: 1rem
      &:nth-child(3n)
        margin-right: 0
    +tablet()
      font-size: 14px
      width: calc((100% / 2) - (1rem / 2))
      &:nth-child(2n)
        margin-right: 0
      &:nth-child(3n)
        margin-right: 1rem
      &:nth-child(6n)
        margin-right: 0
    +phone()
      font-size: 14px
      padding: px2em(25) 0 px2em(25) px2em(25)
      width: 100%
      margin-right: 0!important
      &:first-child
        margin-top: 1rem
    +themed()
      border-left: 5px solid t(hover-primary)
    &:hover
      +themed()
        background-color: t(hover-primary)
        color: white
        border-color: t(hover-primary)
        small
          color: white
    span
      display: block
      font-size: px2em(18)
      text-transform: uppercase
      margin-bottom: .5rem
      font-family: regular, sans-serif
    small
      color: $text-grey


///Биография
.main
  .bio
    &-text-wrapper
      border-top: 1px solid $border
      padding-top: px2em(50)


.main
  .contacts-map
    width: 100%
    margin-top: 3rem
    height: 50vh
    background-color: #fcfaf7
    +tablet()
      height: 30vh
    +phone()
      margin-top: 1rem
      height: 35vh


.main
  .article-viewer
    &-info
      margin-bottom: 1rem
      display: flex
      align-items: center
      .icon
        width: px2em(20)
        height: px2em(20)
        margin-right: .7rem
        fill: $text-grey
      span
        font-size: 1em
        +themed()
          color: t(text-grey)
    &-img
      display: inline-block
      width: 100%
      padding-bottom: 1.2rem
      +phone()
        padding-bottom: 0
      &__wrap
        display: block
    &-footer
      margin-top: 6rem
      +phone()
        margin-top: 3rem
      font-size: px2em(18)
      .category
        display: flex
        &__title
          display: inline-block
          line-height: 1.2
          min-width: 131px
          font-family: medium, sans-serif
          +themed()
            color: t(text)
        &__link
          +themed()
            color: t(text-grey)
          &:hover
            +themed()
              color: t(hover-primary)
          span
            display: inline-block
            +themed()
              color: t(hover-primary)
    +phone()
      font-size: 14px


.feedback
  .feedback-success
    width: 100%
    padding: 2rem
    +themed()
      background-color: t(succes-bg-alert)
    border-radius: 4px
    color: white
    font-size: px2em(25)
    +tablet()
      font-size: 18px
      padding: 1rem

//Структура
.structure
  &__item
    display: block
    padding-top: px2em(10)
  &__item-wrap
    display: block
    padding: .9rem
    border: 1px solid $border
    .position
      font-weight: 500
      font-size: 1em
      +themed()
        color: t(hover-primary)
    .name
      margin: .3rem 0
      font-family: regular, sans-serif
      +themed()
        color: t(text)
    .phone
      +themed()
        color: t(text)
.structure
  ul li
    position: relative
  ul li li
    position: relative
    list-style: none outside none
    border-left: 1px solid $border
    padding: 0 0 0 19px
    line-height: 1.4
    &:after
      content: ''
      display: block
      border-bottom: 1px solid $border
      position: absolute
      width: 18px
      height: 30px
      left: 0
      top: 0
    &:last-child
      border-left: none
      &:after
        border-left: 1px solid $border
  ul
    padding-left: 0
    margin: 0
  ul ul
    margin: 0
    padding-left: 15px
