@use '_mixins/vars' as *;

$themes: (
        default: (
                bg: white,
                bg-accent: $accent,
                bg-primary: $primary,
                text: $text,
                text-accent: $accent,
                text-primary: $primary,
                text-grey: $text-grey,
                text-grey2: $text-grey2,
                hover-primary: $primary,
                hover-accent: $accent,

                top-banner-gb: $accent,

                footer-bg:  #fcfaf7,
                footer_h_color: black,
                footer-about: $text,

                succes-bg-alert: #4caf50

        ),
        dark: (
                bg: black,
                bg-accent: black,
                bg-primary: black,
                text: white,
                text-accent: white,
                text-primary: white,
                text-grey: white,
                text-grey2: white,

                top-banner-gb:  black,

                hover-accent: #7d7d57,
                hover-primary: #7d7d57,

                footer-bg:  black,
                footer_h_color: white,
                footer-about: black,

                succes-bg-alert: #7d7d57
        ),
        yellowed: (
                bg: #f7f3d6!important,
                bg-accent: #f7f3d6!important,
                bg-primary: #f7f3d6!important,
                text: black,
                text-accent: black,
                text-primary: black,
                text-grey: black,
                text-grey2: black,

                top-banner-gb:  #f7f3d6!important,

                hover-accent: #3a5061!important,
                hover-primary: #3a5061!important,

                footer-bg:  #f7f3d6!important,
                footer_h_color: black,
                footer-about: #f7f3d6!important,

                succes-bg-alert: #f7f3d6!important
        ),
        light: (
                bg: #9dd1ff!important,
                bg-accent: #9dd1ff!important,
                bg-primary: #9dd1ff!important,
                text: black,
                text-accent: black,
                text-primary: black,
                text-grey: black,
                text-grey2: black,

                top-banner-gb:  #9dd1ff!important,

                hover-accent: #3a5061!important,
                hover-primary: #3a5061!important,

                footer-bg:  #9dd1ff!important,
                footer_h_color: black,
                footer-about: #9dd1ff!important,

                succes-bg-alert: #9dd1ff!important
        ),
);



@mixin themed() {
  @each $theme, $map in $themes {
    .theme--#{$theme} & {
      $theme-map: () !global;
      @each $key, $submap in $map {
        $value: map-get(map-get($themes, $theme), '#{$key}');
        $theme-map: map-merge($theme-map, ($key: $value)) !global;
      }
      @content;
      $theme-map: null !global;
    }
  }
}

@function t($key) {
  @return map-get($theme-map, $key);
}
