@use 'utils'
@use 'parts'

@use "utils/_mixins/vars" as *
@use "utils/_mixins/media" as *
@use "utils/theme" as *

html, body
  font-size: 16px
body
  font-family: light, sans-serif
  font-size: 16px
  color: $text

article, aside, details, figcaption, figure, footer, header, main, menu, nav, section, summary
  display: block

a.main__link
  +themed()
    color: t(hover-accent)
table
  width: 100%
  border-collapse: collapse
  border-spacing: 0
  margin: 28px 0
p
  line-height: 1.75
  margin-top: 1em
  margin-bottom: 1em
  font-family: light, sans-serif

.main
  +themed()
    background-color: t(bg)
    color: t(text)
  padding: px2em(30) 0
  +desktop()
    padding: px2em(20) 0

.sp--row
  display: flex
  flex-wrap: wrap
  align-items: stretch
  [class*="sp--row-"]
    float: left
    position: relative
    margin: 0
  &-3
    width: calc(100% / 3)
    +tablet()
      width: calc(100% / 2)
    +phone()
      width: 100%
  &-2
    width: calc(100% / 2)

.main
  &--internal-nav
    width: 100%
    font-size: 18px
    ul
      display: flex
      flex-wrap: wrap
      li
        font-family: medium, sans-serif
        margin-right: 1rem
        margin-bottom: .9rem
        border-bottom: 3px solid transparent
        +tablet()
          margin-bottom: .7rem
        &:hover
          +themed()
            color: t(hover-primary)
            border-color: t(hover-primary)
        &.active
          +themed()
            color: t(hover-primary)
            border-color: t(hover-primary)


.main
  &-section
    +themed()
      background-color: t(bg)
      color: t(text)
    padding-top: 3.5%
    +tablet()
      padding-top: 7%
  &__page-header
    font-size: 3em
    margin-top: 2rem
    margin-bottom: 2rem
    &_h2
      &.mb
        margin-bottom: 2rem
    +tablet()
      font-size: 2.2em
      margin-top: 1rem
      &_h2
        font-size: 1.2em
    +phone()
      font-size: 1.7em
  &__interim-header
    font-size: 1.9em
    margin-bottom: 4.5%
    line-height: 1.1
    border-right: 6px solid
    +themed()
      border-color: t(hover-primary)
    +tablet()
      font-size: 1.8em
      margin-bottom: 5%
    +phone()
      font-size: 1.5em
      margin: 8.5% 0
  &__all-view-link
    display: inline-flex
    align-items: center
    font-family: medium, sans-serif
    margin-top: 2.5%
    +tablet()
      margin-top: 1.5%
    &:hover
      +themed()
        color: t(hover-primary)
      .icon
        margin-left: 1rem
    .icon
      width: px2em(25)
      height: px2em(25)
      margin-left: .8rem
      +themed
        fill: t(hover-primary)
      transition: margin-left .3s
.main
  .main--container
    width: 70%
    position: relative
    +wide()
      width: 90%
    +desktop()
      width: 100%

.main
  .main--table
    +phone()
      font-size: 15px
    tr
      +phone()
        display: block
    td
      padding: .5em 0
      font-size: px2em(16)
      vertical-align: inherit
      vertical-align: top
      +phone()
        margin-bottom: 5px
        margin-right: 0
        display: block
    .caption
      min-width: 200px
      +themed()
        color: t(text-grey)
      margin-right: 1rem
      +phone()
        margin-right: 0
        margin-bottom: 0
        padding-bottom: 0
    .description
      font-family: medium, sans-serif
      font-size: px2em(16)


.main
  .main--document-list
    display: flex
    flex-direction: column
  .main--document
    width: 100%
    padding: 2rem 0
    display: flex
    flex-direction: row
    transition: background-color, padding-left .3s
    +phone()
      .icon, .size
        display: none
      border-bottom: 1px solid $border
      &:last-child
        border: none
      .main--document__title
        margin-bottom: 1rem
    &:hover
      padding-left: 1rem
      +themed()
        background-color: t(hover-primary)
        color: white
        .main--document__label
          color: white
        .icon
          fill: white
    .icon
      min-width: 60px
      width: 60px
      min-height: 60px
      height: 60px
      margin-right: 2rem
      +tablet()
        min-width: 40px
        width: 40px
        margin-right: 1rem
      +themed()
        fill: t(hover-primary)
    &-info
      display: flex
      flex-direction: column
      min-width: 1px
    &__title
      font-size: px2em(18)
      font-family: medium, sans-serif
      margin-bottom: 2rem
    &__label
      margin-right: 1rem
      +themed()
        color: t(text-grey)
.main--pagination
  position: relative
  display: flex
  width: 100%
  border-top: 1px solid $border
  padding: 1rem
  align-items: center
  justify-content: center
  text-align: center
  &__page
    font-size: 22px
    font-family: medium, sans-serif
    line-height: 1.75
    a
      position: relative
      display: inline-block
      padding: .7rem 1rem
      line-height: normal
      cursor: pointer
      &:hover
        +themed()
          color: t(hover-primary)
    &_active
      a
        &:before
          content: ''
          position: absolute
          top: -1rem
          left: 0
          right: 0
          height: 5px
          +phone()
            top: 0
          +themed()
            background-color: t(hover-primary)
        +themed()
          color: t(hover-primary)
  +phone()
    padding: 0

.main--not-contented
  width: 100%
  display: flex
  flex-direction: column
  height: 300px
  justify-content: center
  span
    font-size: 1.2em
    margin-bottom: 2rem
    display: block
    +themed()
      color: t(text-grey)
    font-family: regular, sans-serif
  .icon
    width: 150px
    height: 150px
    +phone()
      width: 100px
      height: 100px
    +themed()
      fill: t(text-grey)


.main--article-content
  font-size: px2em(18)
  img
    margin: 1rem 0
  ol
    li
      padding-bottom: .3em
  ul
    margin-left: 1em
    li
      list-style: inside
      padding-bottom: .4em
  table
    tr
      td
        padding: .3em 0
  blockquote
    p
      padding-left: 2rem
      border-left: 5px solid transparent
      +phone()
        padding-left: 1rem
      +themed()
        border-color: t(hover-primary)

.main
  .main--form
    margin-top: 1rem
    &-group
      display: flex
      flex-direction: column
      padding: 0
      border: none
      label
        +themed()
          color: t(text-grey)
        i
          color: $primary
      small
        +themed()
          color: t(text-grey)
    &__control
      display: block
      width: 100%
      margin: 1rem 0
      line-height: 1.1
      font-size: 1em
      padding: px2em(10)
      outline: 0
      border: 1px solid $text-grey
      border-radius: 4px
    &__btn
      align-items: center
      background-clip: padding-box
      +themed()
        background-color: t(hover-primary)
      border: 1px solid transparent
      border-radius: 0.25rem
      box-shadow: rgb(0 0 0 / 2%) 0 1px 3px 0
      box-sizing: border-box
      color: #fff
      cursor: pointer
      display: inline-flex
      font-size: 1em
      justify-content: center
      line-height: 1.25
      margin: 0
      min-height: 3rem
      padding: calc(0.875rem - 1px) calc(1.5rem - 1px)
      position: relative
      text-decoration: none
      transition: all 250ms
      user-select: none
      -webkit-user-select: none
      touch-action: manipulation
      vertical-align: baseline
      width: auto

.theme--light
  color: black!important
